.container {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.2);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 10px;
}

#goal-form {
  width: 30rem;
  max-width: 90%;
  margin: 3rem auto;
  padding: 2rem;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

#goals {
  width: 35rem;
  max-width: 90%;
  margin: 3rem auto;
}
